import React from 'react'

import '../App.css'

function Notification({ correct, points, activeTeam, listTeam, showTeam }) {
  return (

    showTeam && (
      <div className='note' >
        <p>
          {
            correct === true ? `Vi cu ny nhm cc bn c ${points < 0 ? "tr" : "cng"} ${Math.abs(points)} im`
              : correct === false ? (
                <span >
                  Nhm bn  tr li sai
                  <p>{`Xin mi ${listTeam[activeTeam - 1].name} tr li`}</p>
                </span>
              )
                : `n lt ca ${activeTeam <= 3 ? listTeam[activeTeam - 1].name : listTeam[0].name}`
          }
        </p>
      </div>

    )
  )


}

export default Notification